<template>
  <div class="notes--wrapper container">
    <b-alert
      :show="dismissCountDown"
      dismissible
      :variant="message.color"
      @dismissed="dismissCountDown=0"
      @dismiss-count-down="countDownChanged"
    >{{message.text}}</b-alert>

    <form @submit.prevent="postNote(note)" v-if="addNote">
      <h3 class="text-center">Add New Note</h3>
      <input type="text" placeholder="Add a name" class="form-control my-2" v-model="note.name" />
      <input
        type="text"
        placeholder="Add a description"
        class="form-control my-2"
        v-model="note.description"
      />
      <b-button class="btn-sm btn-block btn-success" type="submit">Add</b-button>
    </form>

    <form @submit.prevent="editNote(noteEdit)" v-else>
      <h3 class="text-center">Editar Nota</h3>
      <input
        type="text"
        placeholder="Ingrese un Nombre"
        class="form-control my-2"
        v-model="noteEdit.name"
      />
      <input
        type="text"
        placeholder="Ingrese una descripcion"
        class="form-control my-2"
        v-model="noteEdit.description"
      />
      <b-button class="btn-sm btn-block mb-1 btn-warning" type="submit">Edit</b-button>
      <b-button class="btn-sm btn-block" @click="addNote = true">Cancel</b-button>
    </form>

    <table class="table">
      <thead>
        <tr>
          <th scope="col">#</th>
          <th scope="col">Name</th>
          <th scope="col">Description</th>
          <th scope="col">Date</th>
          <th scope="col">Actions</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in notes" :key="index">
          <th scope="row">{{item._id}}</th>
          <td>{{item.name}}</td>
          <td>{{item.description}}</td>
          <td>{{item.date}}</td>
          <td>
            <b-button class="btn-warning btn-sm mx-2" @click="enableEdit(item._id)">Update</b-button>
            <b-button class="btn-danger btn-sm mx-2" @click="deleteNote(item._id)">Delete</b-button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>
<script>
export default {
  name: "notes",
  data() {
    return {
      notes: [],
      note: {},
      dismissSecs: 5,
      dismissCountDown: 0,
      message: { color: "success", text: "" },
      addNote: true,
      noteEdit: {}
    };
  },
  methods: {
    listNotes() {
      this.axios
        .get("notes")
        .then(response => {
          this.notes = response.data;
        })
        .catch(e => {
          console.log(`error ${e}`);
        });
    },
    postNote(item) {
      const toPopulate = this.notes;
      this.axios
        .post("new-note", item)
        .then(resp => {
          toPopulate.unshift(resp.data);

          // Display alert
          this.showAlert();
          this.message.color = "Success, note added dude";
          this.message.text = "success!";
        })
        .catch(e => {
          console.log(e);
          // Display alert
          this.showAlert();
          this.message.color = "danger";
          this.message.text = e + " fail, you suck!";
        });
    },
    deleteNote(id) {
      this.axios
        .delete(`note/${id}`)
        .then(resp => {
          let index = this.notes.findIndex(item => item._id === resp.data._id);

          // Trigger alert
          this.showAlert();
          this.message.color = "danger";
          this.message.text = "Note deleted!";
          // Delete array position
          this.notes.splice(index, 1);
        })
        .catch(e => {
          console.log(e);
          // Display alert
          this.showAlert();
          this.message.color = "danger";
          this.message.text = e + " fail, you suck!";
        });
    },
    enableEdit(id) {
      this.addNote = false;
      this.axios
        .get(`/note/${id}`)
        .then(res => (this.noteEdit = res.data))
        .catch(e => console.log(e.response));
    },
    editNote(item) {
      this.axios
        .put(`note/${item._id}`, item)
        .then(resp => {
          let index = this.notes.findIndex(
            item => item._id === this.noteEdit._id
          );
          this.notes[index].name = this.noteEdit.name;
          this.notes[index].description = this.noteEdit.description;
          this.noteEdit = {};

          // Trigger alert
          this.showAlert();
          this.message.color = "success";
          this.message.text = "Note edited bro";
        })
        .catch(e => {
          console.log(e);
          // Display alert
          this.showAlert();
          this.message.color = "danger";
          this.message.text = e + " fail, you suck!";
        });
    },
    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown;
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs;
    }
  },
  created() {
    this.listNotes();
  }
};
</script>